.btn {
  color: #6b5770;
  z-index: 0;
  box-sizing: border-box;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  touch-action: manipulation;
  color: #000;
  background-image: linear-gradient(90deg, #5801fa, #bd155b);
  border: 1px solid;
  border-radius: 5px;
  outline: none;
  margin: 0 15px 15px 0;
  padding: 14px 30px;
  font-size: 30px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.btn:before {
  content: "";
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  z-index: -2;
  background: linear-gradient(90deg, #5801fa, #bd155b);
  border-radius: 5px;
  transition: all .4s;
  position: absolute;
  top: -2px;
  left: -2px;
}

.btn:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(90deg, #eedbf5, #fdcee2);
  border-radius: 4px;
  transition: all .4s;
  position: absolute;
  top: 0;
  left: 0;
}

.btn:hover {
  color: #fff;
  transition: all .3s;
}

.btn:hover:after {
  background: linear-gradient(90deg, #896ebd, #db8bac);
}

.btn:active:after {
  background: linear-gradient(90deg, #5801fa, #760f3a);
}

.btn:focus-visible {
  box-shadow: 0 0 0 3px #7b45df;
}

